import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import { Card, CardContent, CardMedia, Typography, Link } from '@mui/material';

const dispatchedEvents = ["ABOUT#clicked"];


/*
    Example: <srs-card>
            </srs-card>
*/
const SrsCard = function (props, ref) {
  return (<Card sx={{ maxWidth: props.max_width || 345 }}>
    <CardMedia
      component="img"
      height="140"
      image={props.image_url || "https://picsum.photos/318/180"}
      alt={props.card_caption || ""}
    />
    <CardContent>
      <Typography gutterBottom variant="h5" component="div">
        {props.card_title}
      </Typography>
      <Typography variant="subtitle1" color="text.secondary">
        {props.card_subtitle}
      </Typography>
      <Typography variant="body2" color="text.secondary">
        Some quick example text to build on the card title and make up the bulk of the card's content.
      </Typography>
      <Link href="#" variant="body2">
        Card Link
      </Link>
      <Link href="#" variant="body2">
        Another Link
      </Link>
    </CardContent>
  </Card>);
}

const styles = `
    
`
const props = [{
  name: "open",
  defaultValue: "false",
  type: "boolean"
}]

const slots = [{ name: "default", allowed: ["srs-accordion-item"] }];

const WrappedComponent = wrapper(React.forwardRef(SrsCard), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };